@import '../../../base';

.header-desc-with-type-date-container {
  min-height: 125px;
  
  .insurer-left-container {
    display: flex;

    @media screen and (max-width: 768px) {
      justify-content: center;
      width: 100%;
      min-width: none;
    }

    @media screen and (max-width: 400px) {
      justify-content: left;
      margin-left: 10px;
    }

    .insurer-logo-container {
      min-width: 150px;
      display: flex;
      justify-content: center;
      align-items: center;

      .company-logo-container {
        display: flex;
        justify-content: center;
        align-items: center;
  
        .company-logo {
          max-width: 70px;
          max-height: 70px;
  
          @media screen and (max-width: 768px) {
            max-width: 40px;
            max-height: 40px;
          }
        }
      }
  
      @media screen and (max-width: 768px) {
        min-height: 80px;
        min-width: 70px;
        margin-right: 10px;
      }
    }

    .insurer-desc-container {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
  
      .insurer-name {
        font-size: 20px;
        font-weight: $font-weight-bold;
        color: $color-title;
        line-height: 10px;
      }
  
      .insurer-desc {
        font-size: $font-size-regular;
        font-weight: $font-weight-semibold;
        color: $color-body;
      }
  
      @media screen and (max-width: 768px) {
        margin-top: 10px;
        margin-left: 10px;

        .insurer-name {
          font-size: $font-size-regular;
        }
    
        .insurer-desc {
          font-size: $font-size-smallest;
        }
      }
    }
  }

  .insurer-right-container {
    padding: 33px;
    padding-right: 30px;
    display: flex;
    flex: 1;
    justify-content: flex-end;

    .data-container {
      display: flex;
      align-items: center;
      height: 100%;

      .data {
        margin-left: 72px;
        
        .data-name {
          font-size: $font-size-regular;
          font-weight: $font-weight-light;
          color: $color-caption;
          line-height: 10px;
        }

        .data-val {
          font-size: $font-size-regular;
          font-weight: $font-weight-normal;
          color: $color-body;
        }
      }
    }

    @media screen and (max-width: 768px) {
      flex: 0 0 100%;
      max-width: 100%;
      padding: 0;
      display: flex;
      justify-content: center;

      .data-container {
        position: relative;

        .data {
          margin: 0 15px;

          .data-name {
            font-size: $font-size-smallest;
          }
  
          .data-val {
            font-size: $font-size-smallest;
          }
        }
      }
    }

    @media screen and (max-width: 400px) {
      justify-content: left;
    }
  }
}