@import '../../../base';

.accordion-border {
  border: solid 1px $color-line;
  border-top: none;
}

.accordion-opened {
  border-top: solid 1px $color-line;
}

.accordion-container {
  width: 100%;

  .accordion-inner {
    padding: 0;

    .card {
      border-radius: 0;
      border: none;
    }
  }

  .expand-container {
    border-top: solid 1px $color-line;
    display: flex;

    .control-container {
      margin: auto;
      border: none;
      background-color: transparent;

      &:focus {
        outline: none;
      }

      .arrow-opened {
        transform: rotate(-180deg);
      }

      span {
        margin-left: 13px;
        font-size: $font-size-regular;
        font-weight: $font-weight-light;
        color: $color-caption;
      }
    }
  }

  .warning-container {
    background-image: linear-gradient(92deg, #ffd138, #ffa41a);
    min-height: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 5px;
    
    p {
      color: white;
      text-align: center;
    }

    @media screen and (max-width: 576px) {
      padding: 13px 28px;
      p {
        font-size: $font-size-smallest;
      }
    }
  }
}
