@import '../../../base';

.form-margin-container {
  max-width: 1036px;
  background-color: white;
  margin: auto;
  margin-top: 15px;
  padding: 0 15px;

  .card-body {
    padding: 0 !important;
  }

  .company-product-container {
    margin-bottom: -1px;
  }

  .form-header {
    text-align: center;
    font-size: $font-size-big;
    font-weight: $font-weight-heavy;
    color: $color-title;

    @media screen and (max-width: 690px) {
      font-size: $font-size-regular;
    }
  }

  .checkout-detail-container-full-border {
    border-left: solid 1px $color-line;
    border-right: solid 1px $color-line;
  }

  .checkout-detail-container {
    border-bottom: solid 1px $color-line;

    .detail-column {
      @media screen and (max-width: 690px) {
        padding: 20px 30px;
      }
      padding: 40px 30px;

      .detail-padding-right {
        margin-right: 40px;
      }

      .detail-padding-left {
        margin-left: 40px;
      }

      @media screen and (max-width: 690px) {
        .detail-padding-right {
          margin-right: 0;
        }

        .detail-padding-left {
          margin-left: 0;
        }
      }

      .detail-header {
        @media screen and (max-width: 690px) {
          font-size: $font-size-big;
        }
        font-size: $font-size-extra-big;
        font-weight: $font-weight-semibold;
        color: $color-title;
      }

      .detail-group {
        @media screen and (max-width: 690px) {
          margin: 6px 0;
        }
        margin: 12px 0;
        
        .detail-desc {
          @media screen and (max-width: 690px) {
            font-size: 12px;
          }
          font-size: $font-size-smallest;
          font-weight: $font-weight-light;
          color: $color-caption;
        }

        .detail-value {
          @media screen and (max-width: 690px) {
            font-size: $font-size-smallest;
          }
          font-size: $font-size-regular;
          font-weight: $font-weight-normal;
          color: $color-body;
          word-break: break-word;
        }
      }
    }
  }

  .expand-container {
    border-top: none;
  }

  .summary-container {
    display: flex;
    flex-direction: column;
    padding: 20px 0;

    @media screen and (min-width: 732px) {
      padding: 30px 20px;
    }

    .summary-value-container {
      display: flex;
      
      span {
        color: $color-caption;
        font-size: $font-size-smallest;
        font-weight: $font-weight-bold;
        
        @media screen and (min-width: 732px) {
          font-size: $font-size-extra-big;
          font-weight: $font-weight-semibold;
        }
      }
  
      .summary-left {
        flex: 1;
        display: flex;
        align-items: center;

        .title {
          min-width: 70px;
          @media screen and (min-width: 732px) {
            min-width: 180px;
          }
        }

        .sub-title {
          font-size: 12px;
          font-weight: $font-weight-light;
          @media screen and (min-width: 732px) {
            font-size: $font-size-regular;
            font-weight: $font-weight-normal;
          }
        }
      }
  
      .sub-text {
        font-size: 12px;
        font-weight: $font-weight-light;
        transform: translateY(-4px);
        @media screen and (min-width: 732px) {
          font-size: $font-size-regular;
          font-weight: $font-weight-normal;
          transform: translateY(-10px);
        }
      }
  
      .sub-text-margin {
        margin-left: 10px;
        @media screen and (min-width: 732px) {
          margin-left: 47px;
        }
      }

      .sub-text-margin2 {
        margin-left: 8px;
        @media screen and (min-width: 732px) {
          margin-left: 39px;
        }
      }
    }
    
    .green {
      color: $color-hover !important;
      span {
        color: $color-hover !important;
      }
    }

    .total {
      margin: 15px 0;
      @media screen and (min-width: 732px) {
        margin: 30px 0;
      }

      .summary-total,
      .summary-left {
        span {
          font-size: $font-size-big;
          font-weight: $font-weight-semibold;
          color: $color-title;

          @media screen and (min-width: 732px) {
            font-size: $font-size-extra-big;
            font-weight: $font-weight-bold;
          }
        }
      }
  
      .summary-total {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        width: 100%;

        .final-amount {
          font-weight: $font-weight-bold;
        }

        .cashback {
          font-size: $font-size-smallest;
          font-weight: $font-weight-normal;
          color: $color-caption;
        }

        @media screen and (min-width: 732px) {
          line-height: 35px;
          .cashback {
            font-size: $font-size-regular;
            font-weight: $font-weight-normal;
          }
        }
      }
    }
  }

  .promo-cashback-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;

    .promo {
      display: flex;
      align-items: center;
      order: 2;
  
      flex: 0 0 100%;
      max-width: 100%;
  
      @media screen and (min-width: 732px) {
        flex: 0 0 45%;
        max-width: 45%;
      }
  
      .input-wrapper-desc {
        font-weight: $font-weight-normal;
      }
  
      @media screen and (max-width: 840px) {
        .button-medium-color p,
        .button-medium-grey p,
        .text {
          font-size: $font-size-smallest;
        }
      }
    }
  
    .cashback {
      flex: 0 0 100%;
      max-width: 100%;
      order: 3;

      @media screen and (min-width: 732px) {
        padding-left: 20px;
        flex: 0 0 55%;
        max-width: 55%;
      }
    }
    
    .cashback-charged {
      @media screen and (min-width: 732px) {
        padding-left: 0;
        flex: 1;
        max-width: 100%;
      }
    }

    .cashback-logged-in {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      order: 1;

      @media screen and (min-width: 732px) {
        order: 3;
      }
      
      .cashback-use-charged {
        min-width: auto !important;
      }

      %cashback-label {
        font-size: $font-size-regular;
        font-weight: $font-weight-semibold;
        color: $color-caption;
      }

      .cashback-use {
        display: flex;
        min-width:270px;
        
        @media screen and (min-width: 732px) {
          min-width:370px;
        }

        .cashback-text {
          @extend %cashback-label;
          position: absolute;
          transform: translateX(-76px);

          @media screen and (min-width: 732px) {
            position: relative;
            transform: translateX(5px);
          }
        }

        .cashback-checkbox {
          flex: 1;
          transform: translateX(139px);
          min-width: 250px;

          @media screen and (min-width: 732px) {
            transform: translateX(88px);
          }
        }
  
        .cashback-discount {
          font-size: $font-size-big;
          font-weight: $font-weight-bold;
          color: $color-hover;
          min-width: 168px;
          text-align: end;
  
          @media screen and (min-width: 732px) {
            font-size: $font-size-extra-big;
            font-weight: $font-weight-heavy;
          }
        }

        .cashback-discount-use {
          color: $color-error-alt-2;
        }
      }

      .cashback-balance {
        display: flex;
        min-width:270px;
        
        @media screen and (min-width: 732px) {
          min-width:350px;
          transform: translateY(-5px);
        }
  
        .cashback-balance-text {
          @extend %cashback-label;
          flex: 1;
          text-align: left;
          transform: translateX(25px);
          @media screen and (min-width: 732px) {
            transform: translateX(55px);
          }
        }
  
        .cashback-balance-display {
          min-width: 128px;
  
          @media screen and (min-width: 732px) {
            min-width: 168px;
          }
  
          .cashback-summary {
            display: flex;
            align-items: center;
            height: 42px;
            img {
              width: 26px;
              height: 26px;
              margin-left: 5px;
            }
            p {
              font-size: $font-size-regular;
              font-weight: $font-weight-heavy;
              color: $color-hover;
              margin: 0;
            }
          }
        }
      }
    }
  }

  .disclaimer {
    color: $color-error-alt-2;
    font-weight: $font-weight-bold;
  }

  .disclaimer-margin {
    margin-top: 30px;
  }

  .disclaimer {
    .input-wrapper-main {
      margin: 5px 0;

      .description {
        color: $color-title;

        button {
          color: $color-hover;
        }
      }
    }
  }

  .promo-remove-button {
    @media screen and (min-width: 576px) {
      margin-left: 20px;
    }
  }

  .checkout-button-container {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;

    .checkout-button {
      margin-left: 22px;
    }

    @media screen and (max-width: 768px) {
      justify-content: center;

      .checkout-button {
          margin: 5px;
          flex: 0 0 100%;
          max-width: 100%;
      }

      .button-medium-grey,
      .button-medium-color {
        max-width: 50%;
      }
    }
  }

  .credit-card-container {
    margin-top: 23px;
    margin-bottom: 60px;
    display: flex;
    justify-content: center;

    @media screen and (min-width: 768px) {
      justify-content: flex-end;
    }
    img {
      height: 37px;
      width: 195px;
    }
  }

  .stripe-checkout-button {
    display: none;
  }

  .charged-header {
    text-align: center;
    font-size: $font-size-big;
    font-weight: $font-weight-heavy;
    color: $color-title;
    margin-bottom: 10px;

    @media screen and (max-width: 690px) {
      font-size: $font-size-regular;
    }
  }
}
